<template>
<div>
    <!--农场-->
    <div class="panel panel-info">
        <div class="panel-body">
            <div>实时监测</div>
            <div>
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <div>
                            <h4>
                                <span v-for="(item,index) in typeCheckData" :key="index" v-html="item.name+'&nbsp;&nbsp;&gt;&nbsp;&nbsp;'" v-show="index<typenum"></span>
                                <span v-html="initData[typenum].title"></span>
                                <a v-if="typenum!=0" class="btn btn-link pull-right" @click="loginItems(-1)">返回上一级</a>
                            </h4>
                        </div>
                    </div>
                    <div class="panel-body">
                        <!--选择位置-->
                        <div v-show="!sensorInfVisible">
                            <div v-if="items.length>0">
                                <div class="col-sm-2 col-md-1 col-lg-1" v-for="(item,index) in items" :key="index">
                                    <button class="btn btn-default" @click="loginItems(1,item)"  v-html="item.name"></button>
                                </div>
                            </div>
                            <div v-show="items.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error">
                                未查找到数据，点击请重试
                            </div>
                        </div>
                        <!--传感器详细信息-->
                        <div v-show="sensorInfVisible">
                            <!--当前信息-->
                            <div v-show="NowAndHistory">
                                <div>
                                    <a class="btn btn-link pull-right" @click="NowAndHistoryWatch(false)">查看历史记录</a>
                                </div>
                                <div id="myChartNow" :style="{width: '600px', height: '500px',margin:'auto'}"></div>
                            </div>
                            <!--历史记录-->
                            <div v-show="!NowAndHistory">
                                <div>
                                    <a class="btn btn-link pull-right"  @click="NowAndHistoryWatch(true)">查看当前数据</a>
                                </div>
                                <div id="myChartHistory" :style="{width: '800px', height: '500px',margin:'auto'}"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import chartFun from '@/echarts/index'
export default {
    data() {
        return {
            sensorInfVisible: false, //是否显示传感器图表数据
            NowAndHistory: true, //当前数据和历史记录切换
            typenum: 0,
            items: [{
                id: 1, // 设备 农场 id
                name: 1 // 设备 等 名称
            }],
            sensorInf: {
                max: 35, // 当前节点种植作物环境上限
                min: 26, // 当前节点种植作物环境下限
                now: 24, // 当前节点当前温度
                type: "温度|气压" //传感器类型
            },
            typeCheckData: [],
            initData: [{
                    url: "sceneFarm",
                    title: "农场"
                },
                {
                    url: "greenHouse",
                    title: "大棚"
                },
                {
                    url: "equip",
                    title: "设备"
                },
                {
                    url: "sensor",
                    title: "传感器",

                },
            ],
        }
    },
    mounted() {
        this.NowAndHistoryWatch(true);
    },
    methods: {
        loginSensorInfData(id) {
            var vm = this;
            vm.sensorInfVisible = true;
            vm.$axios.post(vm.HOST + "", {
                id: id
            }).then(success => {
                vm.sensorInfVisible = true;
                this.NowAndHistoryWatch(true);
            }).catch(error => {})
        },
        loginItems(direction, item = "") {
            var vm = this;
            var typenum = vm.typenum;
            if (typenum == vm.initData.length - 1) {
                if (direction > 0) {
                    vm.loginSensorInfData();
                } else if (direction < 0 && vm.sensorInfVisible) {
                    vm.sensorInfVisible = false;
                    vm.NowAndHistory = true;
                    return;
                }
            }
            if (item != "") {
                vm.typeCheckData[vm.typenum] = item;
            }
            if (direction > 0) {
                vm.typenum += 1;
            } else if (direction < 0) {
                vm.typenum -= 1;
            }
            typenum = vm.typenum;
            if (typenum <= vm.initData.length - 1) {
                vm.loginThisItems(vm.initData[typenum].url, item != '' ? item.id : (vm.typenum == 0 ? "" : vm.typeCheckData[typenum].id))
            } else {
                vm.typenum = vm.initData.length - 1;
                typenum = vm.typenum;
            }
        },
        loginThisItems(url, id = "") {
            var vm = this;
            vm.$axios.post(vm.HOST + url, id != "" ? {
                id: id
            } : "").then(success => {
                vm.items = success.data;
            }).catch(error => {})
        },
        NowAndHistoryWatch(bool) {
            var vm = this;
            if (bool) {
                vm.drawChartNow();
            } else {
                vm.drawChartHistory();
            }
            vm.NowAndHistory = bool;
        },
        //绘制当前信息
        drawChartNow() {
            var vm = this;
            // vm.$axios.post(vm.HOST + "").then(success => { }).catch(error => {})
            let myChart = vm.$echarts.init(document.getElementById('myChartNow'));
            var splitNumber = 10;
            var unittype=vm.drawChartSensorType(0);
            var min = vm.sensorInf.min;
            var max = vm.sensorInf.max;
            var len = max - min;
            if (len > 10 || len < 6) {
                if (len > 10 && len < 20) {
                    splitNumber = 10;
                }
            } else if (len <= 10 && len >= 6) {
                splitNumber = (len) + 4;
            }
            var option = chartFun.sensorInfNow(unittype,splitNumber,min,max,len);
             myChart.setOption(option, true);
            setInterval(function () {
                option.series[0].data[0].value = (Math.random() * 100).toFixed(1) - 0;
                myChart.setOption(option, true);
            }, 2000);

        },
        //绘制历史记录
        drawChartHistory() {
            var vm = this;
            // vm.$axios.post(vm.HOST + "").then(success => { }).catch(error => {})
            let myChart = vm.$echarts.init(document.getElementById('myChartHistory'));
             var unittype=vm.drawChartSensorType(1);
             var xdata=['2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00'];
             var data=[20, 20, 21, 24, 25, 26, 26, 28, 32, 32, 34];
            var option =chartFun.sensorInfHis(unittype,xdata,data);
            myChart.setOption(option, true);
        },
        //传感器类型对应单位和名称
        drawChartSensorType(i){
            var data=[{unit:"℃",name:"温度"},{unit:"Pa",name:"气压"},{unit:" %RH",name:"湿度"}];
            return data[i];
        }
    }
}
</script>

<style scoped>
.logindata-error {
    font-size: 18px;
    color: rgb(243, 29, 29);
    color: #47e64f;
}
</style>



