var options={
    sensorInfNow(unittype={name:"温度",unit:"℃"},splitNumber=10,min=0,max=0,len=0){
        return {
            title: {
                text: unittype.name+'数据仪表图',
                x: 'center'
            },
            tooltip: { //鼠标悬浮交互时的信息提示
                formatter: "{a} <br/>{b} : {c} "+unittype.unit //内容格式
            },
            toolbox: {
                feature: {
                    // restore: {},
                    // saveAsImage: {}
                }
            },
            splitNumber: splitNumber, //仪表盘分隔段数
            series: [{ //驱动图表的数据内容数组s
                min: min - len / (splitNumber - 4) * 2,
                max: max + len / (splitNumber - 4) * 2,
                splitNumber: splitNumber,
                name: unittype.name,
                type: 'gauge',
                detail: {
                    formatter: '{value} '+unittype.unit
                },
                axisLine: {
                    show: true,
                    lineStyle: {
                        color: [
                            [2 / splitNumber, '#ec2a25'],
                            [(splitNumber - 2) / splitNumber, ' #47e64f'],
                            [1, '#ec2a25']
                        ],
                    },
                },
                data: [{
                    value: (max - min) / 2 + min,
                    name: "当前"+unittype.name
                }],
                markPoint: {
                    clickable: false,
                    symbol: 'circle',
                    symbolSize: 5,
                    data: [
                        //跟你的仪表盘的中心位置对应上，颜色可以和画板底色一样
                        {
                            x: 'center',
                            y: 'center',
                            itemStyle: {
                                color: '#FFF'
                            }
                        }
                    ]
                },
                axisLabel: {
                    show: true,
                    formatter: function (value) {
                        return value.toFixed(1);
                    },
                    textStyle: {
                        color: 'auto'
                    }
                },
                axisTick: {
                    splitNumber: 15,
                },
            }],
        };
    },
    /**
     * 
     * @param {Object} unittype 图表单位格式
     * @param {Array} xAxisData x轴坐标名称
     * @param {Array} data 实际数据
     */
    sensorInfHis(unittype={name:"温度",unit:"℃"},xAxisData=[],data=[]){
        return {
            title: {
                text: unittype.name+'数据历史记录曲线图',
                x: 'center'
            },
            xAxis: { //x轴
                name: "时间",
                boundaryGap: false,
                type: 'category',
                axisLabel: {
                    margin: 8
                },
                data: xAxisData,
            },
            yAxis: { // y轴
                name: unittype.name+" (" +unittype.unit+")",
                type: 'value'
            },
            grid: { // 绘图网络
    
            },
            series: [{
                data: data,
                type: 'line',
                smooth: true,
                symbolSize: 8
            }]
        }
    }
};


export default options


